.project-excerpt {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 0;
  padding: 25px 35px;
  border-radius: var(--rounded);
  background: var(--background);
  text-align: left;
  box-shadow: var(--shadow);
  align-items: center;
  transition: transform 0.2s ease, box-shadow 0.2s ease;

  &:hover {
    transform: scale(1.01); // Subtle hover scale effect on the card
    box-shadow: 0px 8px 15px var(--overlay); // Theme-sensitive shadow
  }
}

.project-content {
  flex: 1;
}

.project-title {
  font-weight: var(--semi-bold);
  margin-bottom: 8px;
}

.project-tags {
  margin-bottom: 5px; /* Reduced to minimize space under tags */
}

.project-excerpt-content {
  margin-top: 5px; /* Reduced to minimize space above the excerpt */
}

.project-image-container {
  width: 300px;
  height: 225px;
  overflow: hidden;
  margin-left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px; // Slight rounding for images
  transition: box-shadow 0.2s ease; // Shadow effect without scaling

  &:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.15); // Subtle shadow on hover
  }
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  aspect-ratio: 4 / 3;
}

.read-more {
  color: var(--primary);
  font-weight: var(--semi-bold);
  text-decoration: none;
  margin-left: 5px;

  &:hover {
    text-decoration: underline;
  }
}
